.menu{
    background-color: #066706;
    height: 49px;
    color: #ffffff;
    font-size: 16pt;
   
}
.menu ul li{
    list-style: none;
    transition: 1s;
    position: relative;
}
.menu ul li:hover{
    background-color: rgb(1, 134, 1);
}

.menu_active{
    background-color: rgb(1, 134, 1);
}
.menu_active >a{
    color: #ffffff !important;
}
.menu ul li a{
    color: #ffffff;
    display: block;
    text-decoration: none;
}

.menu ul li ul{
    padding: 0px;
    width: 250px;
    box-shadow: 5px 5px 5px #cccccc;
    position: absolute;
    text-align: left;
    background-color: #ffffff;
    z-index: 10000;
    margin-top: 8px;
    margin-left: -15px;
    display: none;
    transition: 0.5s;
}


.menu ul li ul li{
  padding: 2px 10px 2px 10px;
  position: relative;
  min-width: 200px;
  border-left: solid 3px #ffffff;
  
}
.menu ul li ul li:hover{
    transition: 0.2s;
    background-color: rgb(255, 255, 255);
    border-left: solid 3px darkgreen;
   
}
.menu_li_active{
    background-color: rgb(255, 255, 255);
    border-left: solid 3px darkgreen !important;
}

.menu ul li ul li a:hover{
    margin-left: 5px;
}
.menu ul li ul li ul{
   display: none;
   padding: 0px;
   box-shadow: 5px 5px 5px #cccccc;
   position: absolute;
   text-align: left;
   background-color: #ffffff;
   z-index: 10000;
   margin-top: -31px;
   margin-left:238px;
   display: none;
   transition: 0.5s;
}

.menu ul li ul li a{
    transition: 1s  ;
    color: darkgreen;
    
}

.menu ul li:hover > ul{
    display: block;
}

 .iconChild > a::after{
       content: "\276F";
       font-size: 13pt;
       margin-left: 3px;
       color: black;
      
}
.scrollactive{
    position: fixed;
    width: 100%;
    top: 55px;
    z-index: 10000;
}
.scrollactiveMob{
    position: fixed !important;
    width: 100% !important;
    top: 80px !important;
    z-index: 10000 !important;
}
.scrollactiveMobButtton{
    position: fixed !important;
    top: 80px !important;
}
.menumobile{
    position: relative;
    background-color: #066706;
    margin-top: 0px;
    width: 100%;
    height: 50px;
    z-index: 8000 !important;
}
.menumobile ul{
    width: 230px;
    text-align: left !important;
    margin-top: 50px;
    position: absolute;
    background-color: #ffffff;
    padding-top: 8px;
    display: none;

}
.menumobile ul a{
   display: block;
   color: darkgreen;
   padding-left: 5px;
   border-left: solid 3px #fff;
   text-decoration: none;
   font-size: 14pt;
}
.menumobile ul li{
    text-align: left !important;
    list-style: none;
    padding-left: 10px;
    

}
.menumobile ul li ul{
    margin: 0px;
    padding: 0px;
    position: relative;
    width: 220px;
    display: none;
    
}
.menumobile ul li ul li{
    padding: 0px;
    margin-top: 4px;
}

.menumobile ul li:hover > ul  {
    display: block;
}
.menumobile ul li a:hover{
    border-left: solid 3px darkgreen;
    margin-left: 8px;
    transition: 0.5s;
}
.active{
    transition: 1s;
    transform: rotate(90deg);
   
}
.noActive{
    transition: 1s;
    transform: rotate(0deg);
}
.activeRect{
    transition: 1s;
    margin-top: 6px !important;
    margin-left: -8px !important;
}
.rect{
    transition: 1s;
    width: 30px;
    height: 6px;
    background-color: #000;
    margin-top: 2px;
}
.menumobileButton{
    margin-left: 20px;
    margin-top: 12px;
    position: absolute;
    z-index: 8500;
    cursor: pointer;
}
@media screen and (max-width: 1600px){
    .telList{
        font-size: 15pt;
    } 
   
} 
@media screen and (min-width: 1600px){
    
    .menumobileButton {
        display: none;
    }
} 

@media screen and (max-width: 1140px){
    .messagescrollbox{
        position: absolute;
        margin-top: 30px;
    }
} 
@media screen and (max-width: 800px){
    .container-menu{
        margin-top: 80px;
    }
    .menumobileButton{
        position: fixed !important;
    }
    .menumobile{
        position: fixed !important; 
    } 
   
} 
@media screen and (max-width: 730px){
    .logoscroll{
       display: none !important;
    }
    .headscroll{
        text-align: left !important;
        width: 100% !important;
        margin-left: 15px;
    }
    .telscroll{
        font-size: 10pt !important;
    }
} 
@media screen and (max-width: 570px){
    .telList{
        font-size: 20pt;
      
    } 
    .message{
        width: 90%;
        margin-left: 39px;
    }
    .tel{
        width: 100%; 
        text-align: center;
        padding: 0px 40px 0px 40px!important;
    }
} 
@media screen and (max-width:500px){
    .langscroll{
        position: fixed !important;
        display: initial !important;
        margin-top: 32px !important;
    }
} 
@media screen and (max-width:470px){
    .langscroll{
        margin-left: -50px!important;
    }
} 
@media screen and (max-width:400px){
    .langscroll{
        margin-left: -100px!important;
    }
} 
